<template>
    <!-- 如果购物车没有数据，则显示空页面 -->
    <div v-if="shopnum.length == 0">
      <van-empty description="购物车空空如也，逛一逛" />
    </div>
    <div v-else>
        <div class="car">
            <van-nav-bar
            title="购物车"
            @click-right="manage"
            :fixed="true"
            :right-text="isManage ? '管理' : '完成'"
            />
            <div class="list">
                <van-swipe-cell class="list-items" v-for="(item,index) in shopnum" :key="index">
                    <div class="content">
                        <!-- 单选框 -->
                        <div class="checkbox-item">
                            <van-checkbox v-model="item.isActive" @change="simpleSelect" shape="round" />
                        </div>
                        <div class="content-img">
                            <img class="auto-img" :src="item.img" alt="">
                        </div>
                        <div class='content-message'>
                            <p class="text-o">{{item.title}}</p>
                            <p class="text-n">{{item.style}}/{{item.size}}</p>
                            
                            <div class="product-price">
                                <div class="price fl">￥{{item.price}}</div>
                                <div class="fr product-count">
                                    <van-stepper
                                    v-model= "item.count"
                                    :integer="true"
                                    theme="round"
                                    button-size="22"
                                    disable-input
                                    @change="modifyCount(item)"
                                    />
                                </div>
                            </div>
                            
                        </div>
                    </div>
                    <template #right>
                        <van-button square type="danger" text="删除" @click="removeShopcart(index)"/>
                    </template>
                </van-swipe-cell>
            </div>
            <!-- 提交订单 -->
            <div v-if="isManage">
                <van-submit-bar class="submit-bar" :price="total" @submit="sumbitOrder" :disabled="isNotHasCheck" button-text="提交订单" button-type="info">
                <van-checkbox v-model="allCheck" @click="allSelect">全选</van-checkbox>
                </van-submit-bar>
            </div>
            <div v-else>
                <van-submit-bar class="submit-bar submit-bar-delete" @submit="removemuchs" :disabled="isNotHasCheck" button-text="删除选择" button-type="info">
                <van-checkbox v-model="allCheck" @click="allSelect">全选</van-checkbox>
                </van-submit-bar>
            </div>

        </div>
    </div>
</template>
<script>

    //命名空间用法
    import {createNamespacedHelpers} from "vuex"
    const {mapState,mapMutations}  = createNamespacedHelpers('shopcart')

  export default {
    name:"Car",
    data() {
      return {
        isManage: true,
        allCheck: false,
        total:0
      }
    },
    computed:{
        ...mapState(["shopnum","isNotHasCheck"])
    },
    created(){
        this.sum()
        this.disableds()
    },
    methods:{
        ...mapMutations(['editActive','disabled','disableds','gwcnum','remove','removemuch']),
        //管理购物清单
        manage(){
            this.isManage = !this.isManage
        },

        //全选
        allSelect(){         
            this.editActive(this.allCheck)

            this.disabled(this.allCheck)
            this.sum()
        },

        //单选
        simpleSelect(){
            this.sum()
            this.disableds()

            for (let i = 0; i < this.shopnum.length; i++) {
                if (!this.shopnum[i].isActive){
                    this.allCheck = false
                    return
                }
                
            }
            this.allCheck = true
        },

        //修改购物车数量
        modifyCount(item){
            // 
            this.gwcnum(item)
            this.sum()
        },

        //删除单个
        removeShopcart(index){
            this.remove(index)

            this.sum()
        },
        //删除多个
        removemuchs(){
            this.removemuch()
            this.sum()
        },
        //金额总和
        sum(){
            this.total = 0
            this.shopnum.forEach(element => {
                if(element.isActive){
                    this.total += element.count * element.price * 100
                }
            });
        },


        //提交订单
        sumbitOrder(){
            
            var shopping = []
            this.shopnum.forEach(v=>{
                if(v.isActive){
                    shopping.push(v)
                }
            })
            this.$router.push({name:"Gocar",query:{shopping:shopping}})
        }        

    }
  }
</script>
<style lang="less" scoped>
    .car{
        background-color: #f5f5f5;
        /deep/ .van-nav-bar__text,/deep/ .van-nav-bar__title{
                color: #ffffff;
            }
        /deep/ .van-nav-bar{
            background-color: salmon;
        }
        .list{
            height: calc(~"100vh - 146px");
            margin-top: 46px;
            overflow-y: auto;
            background-color: #fff;
            .list-items:not(:last-child){
                border-bottom: 1px solid #bfbfbf;
            }
             .list-item{
                padding: 10px;
            }
            .content{
                display: flex;
            }
            .checkbox-item{
                width: 20px;
                height: 20px;
                margin: 40px 8px 0 6px ;
                
            }
            .content-img{
                width: 100px;
                height: 100px;
                background-color: #000;
            }
            .content-message{
                margin-left: 8px;
                padding: 0 10px 0 0;
                width: 253px;
               .text-o{
                    font-size: 14px;
                    overflow:hidden; 
                    text-overflow:ellipsis;
                    display:-webkit-box; 
                    -webkit-box-orient:vertical;
                    -webkit-line-clamp:2; 
               }
               .text-n{
                   font-size: 14px;
                   color: #999999;
                   margin: 8px 0;
               }
               .price{
                   color: salmon;
                   font-size: 16px;
               }
            }
            /deep/ .van-stepper__plus{
                background-color: salmon;
            }
            
        }
        /deep/ .van-submit-bar__bar{
            padding: 0 8px;
        }
        /deep/ .van-icon{
                background-color: salmon;
                border-color: salmon;
            }
        /deep/ .submit-bar{
            bottom: 50px;
        }
        /deep/ .van-button--danger{
            height: 100px;
        }
        /deep/ .van-button--info{
            background-color: salmon;
        }
        /deep/ .submit-bar-delete .van-button{
            margin-left: auto;
        }
    }
</style>